.question {
    background: #fefefe;
    border-radius: 8px;
    box-shadow: 0 2px 12px rgba(0, 0, 0, 0.04);
    padding: 24px;

    p {
        color: #29292e
    }

    & + .question {
        margin-top: 8px;
    }

    
    &.highlighted {
        background: #F4f0ff;
        border: 1px solid #835AFD;
        
        footer .user-info span {
            color: #29292e, 100%;
        }
    }

    &.answered {
        background: #dbdcdd;
    }

    p {
        color: #29292e;
    }

    footer {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 24px;

        .userInfo {
            display: flex;
            align-items: center;

            img {
                width: 32px;
                height: 32px;
                border-radius: 50%;
            }

            span {
                margin-left: 8px;
                color: #737380;
                font-size: 14px;
            }
        }

        >div {
            display: flex;
            gap: 8px;
        }

        button {
            border: 0;
            background: transparent;
            cursor: pointer;
            transition: filter 0.2;
            padding: 0;

            &.likeButton {
                display: flex;
                align-items: flex-end;
                color: #737380;
                gap: 8px;

                &.liked {
                    color: #835afd;

                    svg path {
                        stroke: #835afd;
                    }
                }
            }

            &:hover {
                filter: brightness(0.8)
            }
        }
    }
}

